import React from 'react'

import stonelli from "../../assets/img/-stonelli.png";
import maracesh from "../../assets/img/-maracesh.png";

export const HomeTileCollections = () => {
  return (
    <>
        <div className='home__tile--collections__top flex'>
            <h2 className='home__tile--collections__top--title'> </h2>
            <button className='popular--categories--top__more button__more'>
                 
                <div className='border'></div>
            </button>
        </div>
        <div className='home__tile--collections__bottom flex'>
            <div className='home__tile--collections__bottom--card--top'>
                <img className='home__tile--collections--img' src={stonelli} alt="error" />
                <div className='home__tile--collections__bottom--card--bottom flex'>
                    <h3 className='home__tile--collections__bottom--card--bottom__title'> stonelli</h3>
                    <p className='home__tile--collections__bottom--card--bottom__price'>  2 000 ..</p>
                </div>
            </div>
            <div className='home__tile--collections__bottom--card--top'>
                <img className='home__tile--collections--img' src={maracesh} alt="error" />
                <div className='home__tile--collections__bottom--card--bottom flex'>
                    <h3 className='home__tile--collections__bottom--card--bottom__title'> maracesh</h3>
                    <p className='home__tile--collections__bottom--card--bottom__price'>  2 000 ..</p>
                </div>
            </div>
        </div>
    </>
  )
}
